// ProductPage.tsx
import React, { useState, useRef } from 'react';
import {
  InputNumber, Button, Checkbox, Radio, Tag, Popover
} from 'antd';
import './index.less'
import { ShoppingCartOutlined, PayCircleOutlined } from '@ant-design/icons';
import { ProTable } from '@ant-design/pro-components';
export default () => {
  const actionRef = useRef();
  const plainOptions1 = [
    { label: '紫色', value: 'Apple' },
    { label: '绿色 +999-€B', value: 'Pear' },
    { label: '黑色', value: 'Orange' },
  ];
  const plainOptions2 = [
    { label: '大杯', value: 'Apple' },
    { label: '中杯', value: 'Pear' },
    { label: '小杯 +999-€B', value: 'Orange' },
  ];
  const plainOptions3 = [
    { label: '普通包装', value: 'Apple' },
    { label: '精包装 + 999-€B', value: 'Pear' },
  ];
  let columns = [
    {
      title: '序号',
      valueType: 'index',
      width: 80,
    },
    {
      title: '商品',
      dataIndex: 'imgurl',
      width: 150,
      ellipsis: true,
      render: (text: any, record: any) => (
        <>
          {
            <Popover content={() => {
              return (
                <div style={{ display: 'flex', alignItems: 'center' }}>
                  <img src={record.imgurl}
                    style={{
                      width: 350,
                      height: 350,
                      marginRight: 10
                    }}
                  />
                </div>
              )
            }} title="图片">
              <div>
                <img src={record.imgurl}
                  style={{
                    width: 35,
                    height: 35,
                    marginRight: 10,
                    borderRadius: '50%'
                  }}
                />
                <span style={{ cursor: 'pointer' }}>{record.name}</span>
              </div>
            </Popover>
          }
        </>
      ),
    },
    {
      title: '价格',
      dataIndex: 'price',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '数量',
      dataIndex: 'num',
      width: 150,
      ellipsis: true,
      search: false,
    },
    {
      title: '操作',
      dataIndex: 'ssstr',
      width: 150,
      ellipsis: true,
      search: false,
      render: (text: any, record: any) => (
        <Button
          type="link"
          onClick={() => { }}
        >
          查看
        </Button>
      ),
    },

  ];
  let [listData, setListData] = useState(
    Array.from({ length: Math.floor(12) }, (_, i) => ({
      id: `A${i + 1}`,
      imgurl: 'https://blog.itpub.net/ueditor/php/upload/image/20201224/1608797539695919.jpeg',
      name: '商品A',
      price: '￥100',
      priceType: '￥100',
      num: i,
      endTime: Date.now() + 1000 * 60 * 60 * 24 * 3
    }))
  )

  return (
    <div className="shangpxq-box"  >
      <div className="shangpxq-top">
        <div className="left">
          <div className="subA">
            {
              listData.map((item: any, index: number) => {
                return (
                  <>
                    <div className='imgbox'>
                      <img
                        style={{ width: '80px', height: '130px' }}
                        src={item.imgurl} />
                    </div>
                  </>
                )
              })
            }
          </div>
          <div className="subB">
            <img
              src="https://blog.itpub.net/ueditor/php/upload/image/20201224/1608797539695919.jpeg"
              alt="" />
          </div>
        </div>
        <div className="right">
          <div className='title'>Apple iPhone 14 Pro Max</div>
          <div>
            <span className='s1'>889.99-€B</span>
            <span className='s2'>999.99-€B</span>
          </div>
          <div>
            <span>数量  : &nbsp;&nbsp;</span>
            <InputNumber

              min={1} max={10}
              defaultValue={3}
              onChange={() => {

              }} />
          </div>
          <div className='btns'>
            <Button
              type="primary"
              icon={<ShoppingCartOutlined />}>
              加入
            </Button>
            <Button
              danger
              type="primary"
              icon={<PayCircleOutlined />}>
              支付
            </Button>
          </div>
          <h3>商品简介</h3>
          <div className='text'>
            iPhone 14 Pro Max 屏幕解析
            正如书籍的封面决定第一印象，现代智能手机的显示效果已成为用户体验的核心。苹果通过尖端技术实现了：<br />
            ▸ 近乎无边框的屏幕设计，将前置摄像头和扬声器开孔完美隐藏<br />
            ▸ 全球首款ProMotion自适应刷新率显示屏（1Hz-120Hz智能调节）<br />
            ▸ 真实锐利度提升30%的XDR超视网膜屏幕<br />
            ▸ 10亿色彩精准呈现，P3广色域覆盖<br />
            消费者始终称赞加州品牌产品在显示技术上的突破性成就。去年推出的6.7英寸Retina
            XDR面板凭借其革命性的像素密度和对比度，赢得了全球科技媒体的高度评价：<br />
            ▶ 500尼特峰值亮度（阳光下清晰可见）<br />
            ▶ 原生HDR视频拍摄能力<br />
            ▶ 原生AI图像处理芯片加持<br />
          </div>
        </div>
      </div>
      <div className="shangpxq-bottom">
        <h3>颜色</h3>
        <Radio.Group options={plainOptions1}
          defaultValue={['Apple']}
          onChange={() => {

          }} />
        <h3>大小</h3>
        <Radio.Group options={plainOptions2}
          defaultValue={['Apple']}
          onChange={() => {

          }} />
        <h3>包装</h3>
        <Radio.Group options={plainOptions3}
          defaultValue={['Apple']}
          onChange={() => {

          }} />
        <h3>标签</h3>
        <div>
          <Tag color="magenta">magenta</Tag>
          <Tag color="red">red</Tag>
          <Tag color="volcano">volcano</Tag>
          <Tag color="magenta">magenta</Tag>
          <Tag color="red">red</Tag>
          <Tag color="volcano">volcano</Tag>
          <Tag color="magenta">magenta</Tag>
          <Tag color="red">red</Tag>
          <Tag color="volcano">volcano</Tag>
        </div>
        <h3>清单</h3>
        <ProTable
          headerTitle="工单列表"
          actionRef={actionRef}
          rowKey="key"
          toolBarRender={false}
          search={false}
          scroll={{
            y: '400px',
          }}
          request={(params) => {
            // console.log(params);
            return Promise.resolve({
              data: listData,
              success: true,
              total: listData.length
            });
          }}
          columns={columns}
          rowSelection={{
            onChange: (_, selectedRows) => {

            },
          }}
        />
      </div>
    </div>
  );
}